import React from 'react';
import { Navbar, Nav } from 'react-bootstrap';
import { Link, useLocation } from 'react-router-dom';

import './header.scss';
import logo from '../../assets/images/1865721.png';

function Header() {
    const location = useLocation();
    const { pathname } = location;
    const splitLocation = pathname.split("/");

    return (
        <>
            <header>
                <Navbar className="intranet-navbar" bg="dark" variant="dark" expand="lg">
                    <Nav.Link as={Link} to="/">
                        <img src={logo} className="intranet-logo" alt="logo" />
                    </Nav.Link>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className="m-auto">
                            <Link to="/google" className={`intranet-link ${splitLocation[1] === "google" ? "active" : ""}`}>
                                Google
                            </Link>
                            <Link to="/slack" className={`intranet-link ${splitLocation[1] === "slack" ? "active" : ""}`}>
                                Slack
                            </Link>
                        </Nav>
                    </Navbar.Collapse>
                </Navbar>
            </header>
        </>
    );

}

export default Header;